@use './mixins/fluid' as *;
@use 'functions' as *;

@use "vars" as *;
@use "mixins" as *;
@use "fonts" as *;
@use "settings" as *;

.all_pages {
  position: fixed;
  top: 20%;
  right: 0;
  z-index: 101;
  &:hover {
    ul {
      right: 0;
    }
  }
  .page_point {
    padding: 10px 15px;
    background: #00d2ff;
    cursor: pointer;
    color: #fff;
  }
  ul {
    background: #00d2ff;
    padding: 15px 20px;
    position: absolute;
    top: 100%;
    width: 200px;
    right: -200px;
    transition: 0.4s;
    max-height: 450px;
    overflow-y: auto;
    li {
      list-style: none;
      margin-bottom: 15px;
      font-size: 16px;
      a {
        color: #fff;
      }
    }
  }
}

// Header start ========================================================================================================================================================

.header {
  padding-block: 16px;
  background-color: var(--color-light-gray);
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  margin: 0 auto;
  z-index: 100;
  transition: box-shadow 0.3s ease 0s;
  &.scrolling {
    box-shadow: 0 0 5px rgba(0,0,0,0.5);
  }
  @include tablet {
    padding-block: 4px;
  }

  // .header__inner

  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    column-gap: fluid(32, 24);
    @include laptop {
      flex-wrap: wrap;
      row-gap: 10px;
    }
    @include tablet {
      justify-content: flex-start;
    }
  }

  // .header__logo-big

  &__logo-big {
    width: 160px;
    height: 36px;

    @include tablet {
      display: none;
    }
  }

  // .header__logo-sm

  &__logo-sm {
    display: none;

    @include tablet {
      display: block;
    }
  }

  // .header__nav

  &__nav {
    @include laptop {
      flex: 0 1 100%;
      order: 1;
    }
    @include tablet {
      position: fixed;
      top: 52px;
      left: 0;
      right: 0;
      bottom: 0;
      background: var(--color-warm-yellow);
      width: 100%;
      background-color: var(--color-white);
      transform-origin: 50% 0;
      transform: scaleY(0);
      transition: transform 0.3s ease 0s;
      padding-bottom: 15px;
      &.menu--active {
        transform: scaleY(1);
      }
    }
  }

  // .header__action

  &__action {
    display: flex;
    align-items: center;
    column-gap: 8px;
    @include tablet {
      margin-left: auto;
    }
  }

  // .header__checkout

  &__checkout {
    display: flex;
    align-items: center;
    column-gap: 8px;
    span.header__checkout-count {
      display: inline-flex;
      align-items: flex-end;
      width: 20px;
      height: 20px;
      border-radius: 7px;
      background: var(--color-light-blue);
      font-size: 12px;
      font-weight: 400;
      letter-spacing: 0.168px;
      padding: 3px 4px;
      @include tablet {
        position: absolute;
        top: 0px;
        right: -4px;
        width: 18px;
        height: 18px;
      }
      span {
        font-size: 8px;
        letter-spacing: 0.112px;
      }
    }
    @include tablet {
      position: relative;
      width: 44px;
      height: 44px;
      padding: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      span.header__checkout-text {
        display: none;
      }
    }
  }
}

.menu {
  overflow-y: auto;
  > ul {
    display: flex;
    align-items: center;
    column-gap: fluid(24, 16);
    @include laptop {
      background-color: #ececec;
      padding: 15px 25px;
      justify-content: center;
      border-radius: 8px;
    }
    @include tablet {
      background-color: transparent;
      border-radius: 0;
      padding: 0;
      justify-content: flex-start;
      flex-direction: column;
      padding: 8px 15px 8px 15px;
    }
    > li {
      @include tablet {
        background-color: var(--color-light-gray);
        width: 100%;
        &:first-child {
          margin-bottom: 8px;
          border-radius: 8px;
          padding: 24px;
        }
        &:nth-child(2) {
          border-top-left-radius: 8px;
          border-top-right-radius: 8px;
        }
        &:last-child {
          border-bottom-left-radius: 8px;
          border-bottom-right-radius: 8px;
        }
        &:not(:first-child) {
          padding: 0 24px;
          > a {
            width: 100%;
            padding-block: 17px;
            color: var(--color-dark-gray);
            font-size: 14px;
            letter-spacing: 0.224px;
            border-bottom: 1px solid #EEEFF1;
          }
        }
        &:last-child {
          > a {
            border-bottom: 0;
            padding-bottom: 33px;
          }
        }
      }
      > a {
        color: var(--color-dark-navy);
        font-size: 14px;
        letter-spacing: 0.224px;
        position: relative;
        &:after {
          content: '';
          position: absolute;
          left: 0;
          bottom: -5px;
          width: 100%;
          height: 2px;
          background-color: var(--color-light-blue);
          transform-origin: 50% 0;
          transform: scaleX(0);
          transition: transform 0.3s ease 0s;
        }
        @include hover {
          &:after {
            transform: scaleX(1);
            @include tablet {
              transform: scaleX(0);
            }
          }
        }
      }
    }
  }

  // .menu__item

  &__item {
    //position: relative;
    z-index: 1;
  }

  // .menu__btn

  &__btn {
    cursor: pointer;
    position: relative;
    padding-right: 48px;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: 20px;
      width: 20px;
      height: 20px;
      background: url('../img/caret-down.svg') center/20px 20px no-repeat;
    }
    &-active {
      &:after {
        background: url('../img/close.svg') center/20px 20px no-repeat;
      }
    }
    @include tablet {
      background-color: transparent;
      border: none;
      padding: 0;
      color: var(--color-dark-gray);
      font-size: 14px;
      letter-spacing: 0.224px;
      width: 100%;
      text-align: left;
      @include hover {
        background-color: transparent;
      }
      &:after {
        content: none;
      }
      &-active {
        color: var(--color-medium-gray);
      }
    }
  }

  // .menu__mobile-contacts

  &__mobile-contacts {
    display: none;
    > ul {
      margin: 0;
      padding: 0;
      list-style: none;
      display: flex;
      align-items: center;
      column-gap: 16px;
      > li {
        > a {
          display: inline-flex;
          align-items: center;
          justify-content: center;
          width: 48px;
          height: 48px;
          border-radius: 999px;
          border: 2px solid var(--color-deep-blue);
          transition: border-color 0.3s ease 0s;
          svg {
            path {
              transition: fill 0.3s ease 0s;
            }
          }
          @include hover {
            border-color: var(--color-light-blue);
            svg {
              path {
                fill: var(--color-light-blue);
              }
            }
          }
        }
      }
    }
    @include tablet {
      display: block;
    }
    @include tablet {
      background-color: var(--color-light-gray);
      border-radius: 8px;
      margin-inline: 15px;
      padding: 24px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      row-gap: 24px;
      .header__contact {
        width: 90%;
        text-align: center;
        display: block;
        background-color: var(--color-deep-blue);
        color: var(--color-white);
      }
    }
  }
}

.dropdown {
  position: absolute;
  left: 0;
  top: 100%;
  z-index: 2;
  border-top: 1px solid var(--color-light-gray);
  padding-inline: calc((100% - var(--container-width)) / 2);
  padding-block: fluid(32, 24);
  height: min-content;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease 0s, visibility 0.3s ease 0s;
  width: 100%;
  background-color: var(--color-white);
  > ul {
    padding-inline: var(--container-offset);
  }
  &-active {
    opacity: 1;
    visibility: visible;
  }
  @include tablet {
    position: static;
    height: 0;
    border-top: 0;
    padding: 0;
    background-color: transparent;
    &-active {
      opacity: 1;
      visibility: visible;
      @include tablet {
        height: auto;
      }
    }
  }

  // .dropdown__list

  &__list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 32px;
    @include laptop {
      gap: 24px;
    }
    @include tablet {
      display: flex;
      gap: 0;
      flex-direction: column;
      padding: 0 !important;
    }
  }

  // .dropdown__item

  &__item {
    position: relative;
    &:nth-child(odd) {
      &:after {
        content: '';
        position: absolute;
        top: 16px;
        left: 16px;
        width: 60px;
        height: 60px;
        background: url('../img/arrow-bold-yellow.svg') left top/60px 60px no-repeat;
      }
      .dropdown__img {
        background: var(--color-deep-blue);
      }
    }
    &:nth-child(even) {
      &:after {
        content: '';
        position: absolute;
        top: 16px;
        left: 16px;
        width: 60px;
        height: 60px;
        background: url('../img/arrow-bold-white.svg') left top/60px 60px no-repeat;
      }
      .dropdown__img {
        background: var(--color-golden-yellow);
      }
    }
    @include tablet {
      &:last-child {
        .dropdown__link {
          border-bottom: none;
        }
      }
      &:after {
        content: none !important;
      }
    }
  }

  // .dropdown__img

  &__img {
    border-radius: 8px;
    opacity: 0.6;
    overflow: hidden;
    height: 172px;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
    }
    @include tablet {
      display: none;
    }
  }

  // .dropdown__link

  &__link {
    @include fluid-text(20, 16);
    display: flex;
    justify-content: space-between;
    align-items: center;
    column-gap: 25px;
    padding: 24px;
    border-bottom: 2px solid var(--color-medium-gray);
    transition: color 0.3s ease 0s, border-color 0.3s ease 0s;
    color: var(--color-dark-navy);
    letter-spacing: 0.32px;
    svg {
      --widthSVG: 28px;

      width: var(--widthSVG);
      height: var(--widthSVG);
      stroke {
        transition: fill 0.3s ease 0s;
      }

      @include tablet {
        --widthSVG: 32px;
      }
    }
    @include hover {
      color: var(--color-deep-blue);
      border-color: var(--color-deep-blue);
      svg {
        path {
          stroke: var(--color-deep-blue);
        }
      }
    }
    @include tablet {
      padding-block: 16px;
      padding-inline: 0;
      border-bottom: 2px solid rgba(208, 208, 208, 0.20);
      font-size: 16px;
    }
  }
}

.burger {
  @include burger;

  display: none;

  @include tablet {
    display: block;
  }
}

// Header end ========================================================================================================================================================

// Section hero start ========================================================================================================================================================

.hero {
  padding: 76.8px 8px 4px 8px;
  background-color: var(--color-light-gray);
  @include laptop {
    padding: 161.59px 8px 4px 8px;
  }
  @include tablet {
    padding: 60px 8px 4px 8px;
  }

  // .hero__inner

  &__inner {
    border-radius: 8px;
    overflow: hidden;
    background: var(--bg-image-desktop) center/cover no-repeat;
    padding: fluid(580, 321) 0 fluid(48, 24);

    @include laptop {
      background: var(--bg-image-laptop) center/cover no-repeat;
    }

    @include tablet {
      background: var(--bg-image-tablet) center/cover no-repeat;
    }

    @include mobile {
      background: var(--bg-image-mobile) center/cover no-repeat;
    }
  }

  // .hero__title

  &__title {
    @include fluid-text(72, 32);

    max-width: 790px;
    color: var(--color-white);
    font-family: "eUkraineHead";
    font-weight: 400;
    letter-spacing: fluid(0.864, 0.486);
    margin-bottom: fluid(48, 24);
    @include tablet {
      font-weight: 300;
    }
    @include mobile-s {
     font-family: var(--font-family, sans-serif);
    }
  }

  // .hero__btn

  &__btn {
    max-width: 550px;
    @include tablet {
      max-width: 460px;
    }
    @include mobile-s {
      max-width: none;
    }
  }
}

// Section hero end ========================================================================================================================================================

// Section about us start ========================================================================================================================================================

.about-us {
  padding: 4px 8px;
  background-color: var(--color-light-gray);

  // .about-us__inner

  &__inner {
    border-radius: 8px;
    padding-block: fluid(132, 64);
    background-color: var(--color-white);
  }

  // .about-us__wrapper

  &__wrapper {
    display: flex;
    column-gap: 32px;
    @include mobile-m {
      flex-direction: column;
      row-gap: 32px;
    }
  }

  // .about-us__title

  &__title {
    --widthBody: 438px;

    flex: 0 1 var(--widthBody);
    @include tablet {
      --widthBody: 274px;
      flex: 0 1 274px;
    }
    @include mobile {

    }
    @include mobile-m {
      flex: 0 1 100%;
      padding-bottom: 24px;
      border-bottom: 1px solid var(--color-medium-gray);
    }
  }

  // .about-us__body

  &__body {
    --widthBody: 438px;
    @include tablet {
      --widthBody: 274px;
    }
    @include mobile {
      --widthBody: 30%;
    }
    flex: 0 1 calc(100% - var(--widthBody));
    display: flex;
    flex-direction: column;
    row-gap: 32px;
    @include tablet {
      row-gap: 24px;
    }
    @include mobile-m {
      flex: 0 1 100%;
    }
    p {
      @include fluid-text(20, 16);

      margin: 0;
      padding: 0;
      color: var(--color-dark-gray);
      letter-spacing: fluid(0.32, 0.256);
      span {
        color: var(--color-dark-navy);
      }
    }
  }
}

// Section about us end ========================================================================================================================================================

// Section numbers start ========================================================================================================================================================

.numbers {
  padding: 4px 8px;
  background-color: var(--color-light-gray);

  // .numbers__inner

  &__inner {
    border-radius: 8px;
    padding-block: fluid(132, 64);
    background-color: var(--color-white);
  }

  // .numbers__wrapper

  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    column-gap: 32px;
    padding-bottom: fluid(32, 24);
    border-bottom: 1px solid var(--color-medium-gray);
    margin-bottom: 32px;
    @include mobile-m {
      flex-direction: column;
      row-gap: 24px;
      align-items: flex-start;
    }
  }

  // .numbers__title

  &__title {
    flex: 0 1 calc(50% - 16px);
  }

  // .numbers__descr

  &__descr {
    flex: 0 1 calc(50% - 16px);
    p {
      @include fluid-text(20, 14);
      color: var(--color-dark-gray);
      letter-spacing: fluid(0.32, 0.224);
    }
  }

  // .numbers__list

  &__list {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 32px;
    margin-bottom: 32px;

    @include tablet {
      gap: 16px;
    }

    @include mobile {
      grid-template-columns: 1fr;
    }
  }

  // .numbers__item

  &__item {
    border-radius: 8px;
    overflow: hidden;
    background: var(--color-light-gray);
    position: relative;
    padding: 24px;
    &:nth-child(-n + 2) {
      grid-column: span 3;
      height: fluid(400, 200);
      @include mobile {
        grid-column: span 1;
      }
    }
    &:nth-child(n + 3) {
      height: fluid(320, 172);
    }
    &:nth-child(3), &:nth-child(4) {
      grid-column: span 3;
      @include mobile {
        grid-column: span 1;
      }
    }
    &:nth-child(n + 5) {
      grid-column: span 2;
      @include mobile {
        grid-column: span 1;
      }
    }
    &:nth-child(1) {
      background: var(--color-dark-navy);
      background-repeat: no-repeat;
      background-size: 609px 400px;
      background-position: 100% 50%;
      .numbers__info {
        p, span {
          color: var(--color-white);
        }
      }
    }
    &:nth-child(2) {
      background-color: var(--color-deep-blue);
      img {
        position: absolute;
        top: fluid(24, 16);
        left: fluid(24, 16);
      }
      @include mobile {
        order: -1;
      }
    }
    &:nth-child(3) {
      background-size: 504px 320px;
      background-repeat: no-repeat;
      background-position: 95% 50%;
    }
    &:nth-child(4) {
      background-size: 435px 320px;
      background-repeat: no-repeat;
      background-position: 115% 50%;
    }
  }

  // .numbers__info

  &__info {
    position: absolute;
    left: 24px;
    bottom: 24px;
    span {
      color: var(--color-dark-navy);
      margin-bottom: 8px;
    }
    span:first-child {
      @include fluid-text(72, 36);
      font-family: "eUkraineHead";
      letter-spacing: fluid(0.624, 0.486);
    }
    span:last-child {
      @include fluid-text(22, 20);
      letter-spacing: fluid(0.352, 0.32);
    }
    p {
      @include fluid-text(16, 12);
      color: var(--color-dark-gray);
      line-height: 1.4;
      letter-spacing: fluid(0.256, 0.192);
      @include tablet {
        line-height: 1.2;
      }
    }
  }
}

// Section numbers end ========================================================================================================================================================

// Section work start ========================================================================================================================================================

.work {
  padding: 4px 8px;
  background-color: var(--color-light-gray);

  // .work__inner

  &__inner {
    border-radius: 8px;
    padding-block: fluid(132, 64);
    background-color: var(--color-white);
  }

  // .work__wrapper

  &__wrapper {
    padding-bottom: fluid(32, 24);
    border-bottom: 1px solid var(--color-medium-gray);
    margin-bottom: 32px;
  }

  // .work__body

  &__body {
    display: grid;
    grid-template-columns: 469px 1fr;
    column-gap: 32px;
    @include tablet-over {
      grid-template-columns: 352px 1fr;
    }
    @include tablet {
      grid-template-columns: 1fr;
      background-color: var(--color-light-gray);
      border-radius: 16px;
      padding: 24px;
    }
    @include mobile-s {
      padding: 0;
      background-color: var(--color-white);
    }
  }

  // .work__slider

  &__slider {
    max-width: 100%;
    background-color: var(--color-light-gray);
    border-radius: 16px;
    @include tablet {
      order: 1;
    }
    @include mobile-s {
      background-color: var(--color-light-gray);
    }
  }

  // .work__slide

  &__slide {
    padding: 40px;
    display: flex;
    flex-direction: column;
    background-color: var(--color-light-gray);
    @include tablet {
      background-color: var(--color-white);
    }
    @include tablet-over {
      padding: 24px;
    }
    @include mobile-s {
      background-color: var(--color-light-gray) !important;
    }
  }

  // .work__slide-icon

  &__slide-icon {
    --widthIconWrap: 92px;

    @include square(var(--widthIconWrap));

    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--color-light-blue);
    border-radius: 16px;
    margin-bottom: 32px;

    @include tablet {
      --widthIconWrap: 72px;
    }

    svg {
      --widthSvgIcon: 52px;

      @include square(var(--widthSvgIcon));
      fill: var(--color-white);

      @include tablet {
        --widthSvgIcon: 40px;
      }
    }

  }

  // .work__slide-title

  &__slide-title {
    @include fluid-text(28, 24);

    color: var(--color-dark-navy);
    letter-spacing: fluid(0.392, 0.36);
    margin-bottom: 16px;
  }

  // work__slide-text

  &__slide-text {
    p {
      @include fluid-text(16, 14);

      color: var(--color-dark-gray);
      line-height: 1.4;
      letter-spacing: fluid(0.256, 0.224);
      @include tablet {
        line-height: 1.2;
      }
    }
    margin-bottom: fluid(50, 40);
    @include mobile-s {
      margin-bottom: 73px;
    }
  }

  // .work__btn

  &__btn {
    margin-bottom: 16px;
    flex-grow: 1;
    max-height: max-content;
    margin-top: auto;
    @include tablet {
      max-width: 272px;
    }
    @include mobile-m {
      max-width: none;
    }
  }

  // .work__steps

  &__steps {
    background-color: var(--color-light-gray);
    padding: 40px;
    border-radius: 16px;

    @include tablet {
      padding: 0;
      margin-bottom: 40px;
    }

    @include mobile-s {
      display: none;
    }
  }

  // .work__steps-develop

  &__steps-develop {
    color: var(--color-dark-navy);
    font-size: 28px;
    letter-spacing: 0.392px;
    margin-bottom: 8px;
    @include tablet {
      display: none;
    }
  }

  // .work__text

  &__text {
    color: var(--color-medium-gray);
    font-size: 14px;
    letter-spacing: 0.224px;
    margin-bottom: 44px;
    @include tablet {
      display: none;
    }
  }

  // .work__image

  &__image {
    svg {
      width: 100%;
      height: auto;
    }
  }

  .swiper-button-next,
  .swiper-button-prev {
    &:after {
      content: '';
    }

    @include square(56px);

    margin-top: 0;
    top: 40px;
    border-radius: 50%;
    background-color: var(--color-light-gray);
    border: 1px solid var(--color-deep-blue);
    display: inline-flex;
    align-items: center;
    justify-content: center;
    svg {
      width: 24px;
      height: 24px;
      fill: var(--color-deep-blue);
      @include tablet-over {
        width: 20px;
        height: 20px;
      }
    }
    @include tablet-over {
      @include square(40px);

      top: 24px;
    }
    @include tablet {
      top: auto;
      bottom: 35px;
    }
    @include mobile-m {
      bottom: auto;
      top: 24px;
    }
  }

  .swiper-button-next {
    right: 40px;
    @include tablet-over {
      right: 24px;
    }
  }

  .swiper-button-prev {
    right: 120px;
    left: auto;
    @include tablet-over {
      right: 80px;
    }
  }

  .swiper-scrollbar {
    bottom: 40px;
    max-width: calc(100% - 80px);
    left: 50%;
    transform: translateX(-50%);
    height: 7px;
    border-radius: 6px;
    background-color: var(--color-medium-gray);
    @include tablet-over {
      bottom: 24px;
      max-width: calc(100% - 48px);
    }
    @include tablet {
      max-width: 272px;
      left: 24px;
      transform: translateX(0);
    }
    @include mobile-m {
      max-width: calc(100% - 48px);
    }
  }

  .swiper-scrollbar-drag {
    background-color: var(--color-golden-yellow);
  }

  .swiper, .swiper-wrapper, .swiper-slide {
    max-width: 100% !important;
  }
}

// Section work end ========================================================================================================================================================

// Section testimonials start ========================================================================================================================================================

.testimonials {
  padding: 4px 8px;
  background-color: var(--color-light-gray);

  // .testimonials__inner

  &__inner {
    border-radius: 8px;
    padding-block: fluid(132, 64);
    background-color: var(--color-white);
  }

  // .testimonials__wrapper

  &__wrapper {
    background-color: var(--color-dark-navy);
    border-radius: 8px;
    padding-block: fluid(64, 32);
    padding-inline: 24px;
    margin-bottom: fluid(32, 16);
  }

  // .testimonials__title

  &__title {
    padding-bottom: fluid(32, 24);
    border-bottom: 2px solid var(--color-light-gray);
  }

  // .testimonials__info

  &__info {
    padding-top: fluid(32, 24);
    display: flex;
    align-items: center;
    justify-content: space-between;
    column-gap: 30px;
    @include mobile {
      flex-direction: column;
      row-gap: 32px;
    }
  }

  // .testimonials__descr

  &__descr {
    max-width: 772px;
    p {
      @include fluid-text(20, 14);

      color: var(--color-white);
      letter-spacing: fluid(0.32, 0.224);
    }
  }

  // .testimonials__slide

  &__slide {
    border-radius: 8px;
    overflow: hidden;
  }

  // .testimonials__link

  &__link {
    width: 100%;
    height: 100%;
    position: relative;
    display: block;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
    }
  }

  // .testimonials__play

  &__play {
    @include square(72px);

    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: var(--color-deep-blue);
    border-radius: 50%;
    transition: background-color 0.3s ease 0s;
    display: flex;
    justify-content: center;
    align-items: center;
    svg {
      width: 28px;
      height: 28px;
    }
    @include hover {
      background-color: var(--color-light-blue);
    }
    @include mobile-m {
      @include square(56px);

      svg {
        width: 24px;
        height: 24px;
      }
    }
  }

  // .testimonials__swiper-buttons

  &__swiper-buttons {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 32px;
    @include tablet {
      grid-template-columns: repeat(3, 1fr);
      column-gap: 16px;
    }
    @include mobile {
      grid-template-columns: repeat(2, 1fr);
    }
  }

  .swiper {
    @include mobile {
      padding-bottom: 31px;
    }
  }

  .swiper-scrollbar {
    bottom: 0;
    height: 7px;
    background-color: var(--color-medium-gray);
    display: none;
    @include mobile {
      display: block;
    }
  }

  .swiper-scrollbar-drag {
    background-color: var(--color-light-blue);
  }

  .swiper-wrapper {
    padding-bottom: fluid(32, 16);
  }

  .swiper-button-prev,
  .swiper-button-next {
    &:after {
      content: none;
    }
    top: auto;
    bottom: 0px;
    margin: 0;
  }

  .swiper-button-testimonials-next {
    grid-column: span 3;
    @include tablet {
      grid-column: span 2;
    }
    @include mobile {
      grid-column: span 1;
    }
    a {
      padding: 14px 24px;
    }
  }
  .swiper-button-testimonials-prev {
    grid-column: span 1;
  }
  .swiper-button-testimonials-next,
  .swiper-button-testimonials-prev {
    @include hover {
      svg {
        path {
          fill: var(--color-white);
        }
      }
    }
  }
}

.social {
  display: flex;
  column-gap: 16px;

  // .social__item

  &__item {
    --itemSocialSquare: 72px;

    @include square(var(--itemSocialSquare));

    @include tablet {
      --itemSocialSquare: 48px;
    }
    @include mobile-m {
      --itemSocialSquare: 56px;
    }
  }

  // .social__link

  &__link {
    width: 100%;
    height: 100%;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    background-color: var(--color-deep-blue);
    border-radius: 50%;
    transition: background-color 0.3s ease 0s;
    @include hover {
      background: var(--color-light-blue);
    }
    svg {
      --iconSocialSquare: 28px;

      width: var(--iconSocialSquare);
      height: var(--iconSocialSquare);

      @include tablet {
        --iconSocialSquare: 20px;
      }
      @include mobile-m {
        --iconSocialSquare: 24px;
      }
    }
  }
}

// Section testimonials end ========================================================================================================================================================

// Page checkout start ========================================================================================================================================================

.checkout {
  background-color: var(--color-light-gray);
  padding: 76.8px 8px 100px 8px;
  @include laptop {
    padding: 161.59px 8px 4px 8px;
  }
  @include tablet {
    padding: 60px 8px 4px 8px;
  }

  // .checkout__title

  &__title {
    background-color: var(--color-white);
    padding: fluid(72, 32) fluid(32, 16) fluid(32, 24);
    border-radius: 16px;
    @include mobile {
      border-radius: 16px 16px 0 0;
      padding: 32px 16px 0 16px;
    }
  }

  // .checkout__form

  &__form {
    display: grid;
    column-gap: 8px;
    grid-template-columns: 1fr 645px;
    @include laptop {
      grid-template-columns: 1fr 595px;
    }
    @include tablet {
      grid-template-columns: 1fr 420px;
    }
    @include mobile {
      grid-template-columns: 1fr;
    }
  }

  // .checkout__column

  &__column {
    &:first-child {
      .checkout__row {
        background-color: var(--color-white);
        border-radius: 16px;
        margin-top: 8px;
        padding: 32px;
        @include laptop {
          padding: 32px 16px;
        }
      }
      .checkout__name {
        margin-bottom: fluid(56, 24);
      }
    }
    &:last-child {
      background-color: var(--color-white);
      border-radius: 16px;
      padding: 32px;
      margin-top: 8px;
      @include laptop {
        padding: 32px 16px;
      }
    }
    @include mobile {
      display: contents;
    }
  }

  // .checkout__customer-contacts

  &__customer-contacts {
    .checkout__field:first-of-type {
      margin-bottom: 32px;
    }
    input {
      border: 2px solid var(--color-medium-gray);
      border-radius: 8px;
      background-color: var(--color-white);
      color: var(--color-dark-navy);
      font-size: 14px;
      letter-spacing: 0.224px;
      padding: 15px 16px;
      &::placeholder {
        color: var(--color-dark-gray);
      }
    }
  }

  // .checkout__name

  &__name {
    @include fluid-text(24, 20);
    color: var(--color-dark-navy);
    letter-spacing: fluid(0.36, 0.32);
  }

  // .checkout__delivery

  &__delivery {
    .choices__inner {
      border-radius: 8px;
      border: 2px solid var(--color-medium-gray);
      background: var(--color-white);
      padding: 13px 16px !important;
      @include laptop {
        font-size: 12px;
      }
      @include tablet-over {
        font-size: 14px;
      }
    }
    .choices__list {
      line-height: 1.4;
      padding: 0;
      border-radius: 16px;
    }
    .choices[data-type*=select-one]::after {
      border: 0;
      width: 20px;
      height: 20px;
      right: 18px;
      top: 50%;
      transform: translateY(-50%);
      margin-top: 0;
      background-position: center;
      background-size: 20px 20px;
      background-repeat: no-repeat;
    }
    .checkout__field-col:first-child {
      .choices[data-type*=select-one]::after {
        background-image: url('../img/glass.svg');
      }
    }
    .checkout__field-col:last-child {
      .choices[data-type*=select-one]::after {
        background-image: url('../img/caret-down-dark.svg');
      }
    }
  }

  &__customer-contacts, &__delivery {
    .checkout__field {
      display: flex;
      gap: 32px;
      @include laptop {
        gap: 16px;
      }
      @include tablet-over {
        flex-direction: column;
        row-gap: 16px;
      }
    }
    .checkout__field-col {
      flex: 0 1 50%;
      display: flex;
      flex-direction: column;
      row-gap: 8px;
    }
    label {
      color: var(--color-dark-gray);
      font-size: 14px;
      font-weight: 400;
      letter-spacing: 0.196px;
    }
  }

  // .checkout__communication

  &__communication {
    .radio-label {
      font-size: 0;
    }
    .radio-field {
      display: none;
      &:checked + .radio-content {
        background-color: var(--color-deep-blue);
        color: var(--color-white);
        &:after {
          background: url('../img/circle-active.svg') center/24px 24px no-repeat;
        }
      }
    }
    .radio-content {
      color: var(--color-dark-gray);
      font-size: 14px;
      letter-spacing: 0.224px;
      border-radius: 8px;
      background: var(--color-light-gray);
      padding: 12px 16px 12px 48px;
      display: inline-block;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        width: 24px;
        height: 24px;
        top: 50%;
        left: 12px;
        transform: translateY(-50%);
        background: url('../img/circle.svg') center/24px 24px no-repeat;
      }
    }

  }

  // .checkout__pre-order

  &__pre-order {
    .checkout__name {
      margin-bottom: 16px;
      @include mobile {
        display: none;
      }
    }
    @include mobile {
      order: -1;
      background-color: var(--color-white);
      padding: 0 16px 16px 16px;
      border-radius: 0 0 16px 16px;
    }
  }

  // .checkout__total

  &__total {
    padding-block: 32px;
    margin-block: 32px;
    border-bottom: 1px solid var(--color-medium-gray);
    border-top: 1px solid var(--color-medium-gray);
    @include mobile {
      background-color: var(--color-white);
      padding: 0 16px 24px 16px;
      border: 0;
      border-radius: 16px 16px 0 0;
      margin: 0;
      margin-top: 8px;
    }
  }

  // .checkout__cost

  &__cost {
    color: var(--color-dark-gray);
    font-size: 14px;
    letter-spacing: 0.224px;
    @include mobile {
      padding-top: 32px;
    }
  }

  // .checkout__sum

  &__sum {
    color: var(--color-dark-navy);
    font-size: 28px;
    letter-spacing: 0.392px;
    display: inline-block;
    margin-bottom: 16px;
    span {
      font-size: 16px;
      line-height: 1.4;
    }
  }

  // .checkout__military

  &__military {
    background-color: var(--color-light-gray);
    padding: 9px 9px 9px 44px;
    @include mobile {
      padding: 11px 11px 11px 42px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 0;
        bottom: -24px;
        width: 100%;
        height: 1px;
        background-color: var(--color-medium-gray);
      }
    }
    p {
      @include fluid-text(16, 14);

      color: var(--color-dark-gray);
      line-height: 1.4;
      letter-spacing: fluid(0.256, 0.224);
      span {
        color: var(--color-dark-navy);
      }

      @include mobile {
        line-height: 1.2;
      }
    }
  }

  // .checkout__buttons

  &__buttons {
    padding-block: 24px;
    @include mobile {
      background-color: var(--color-white);
      border-radius: 0 0 16px 16px;
      padding: 24px 16px 32px;
    }
  }

  // .checkout__btn

  &__btn {
    display: block;
    width: 100%;
    padding-block: 25px;
    &:first-child {
      margin-bottom: 10px;
    }
    @include tablet {
      padding-block: 18px;
    }
    @include mobile {
      padding-block: 15px;
    }
  }

  // .checkout__info

  &__info {
    p {
      @include fluid-text(14, 12);

      margin-top: fluid(40, 24);
      color: var(--color-dark-gray);
      text-align: center;
      letter-spacing: fluid(0.224, 0.192);
    }
  }
}

.tab {
  display: flex;
  column-gap: 16px;
  row-gap: 16px;
  margin-bottom: fluid(32, 24);
  flex-wrap: wrap;

  // .tab__content

  &__content {
    label {
      color: var(--color-dark-gray);
      font-size: 14px;
      font-weight: 400;
      letter-spacing: 0.196px;
      display: block;
      margin-bottom: 8px;
    }
    input {
      width: 100%;
      border: 2px solid var(--color-medium-gray);
      border-radius: 8px;
      background-color: var(--color-white);
      color: var(--color-dark-navy);
      font-size: 14px;
      letter-spacing: 0.224px;
      padding: 15px 16px;
      &::placeholder {
        color: var(--color-dark-gray);
      }
    }
  }
}

.preorder {

  // .preorder__item

  &__item {
    position: relative;
    background-color: var(--color-light-gray);
    padding: 8px 16px 8px 8px;
    border-radius: 8px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(2, 62px);
    &:not(:last-child) {
      margin-bottom: 16px;
    }
    @include tablet {
      grid-template-rows: 116px 36px;
      grid-gap: 16px;
    }
    @include mobile {
      grid-template-columns: repeat(5, 1fr);
      grid-template-rows: repeat(2, 50px);
      margin-top: 24px;
    }
    @include mobile-m {
      grid-template-rows: 116px 36px;
      grid-gap: 16px;
    }
  }

  // .preorder__trash

  &__trash {
    position: absolute;
    top: 24px;
    right: 16px;
    @include tablet {
      top: 8px;
    }
    svg path {
      transition: fill 0.3s ease 0s;
    }
    @include hover {
      svg path {
        fill: var(--color-error);
      }
    }
  }

  // .preorder__image

  &__image {
    grid-column: 1 / 2;
    grid-row: 1 / 2;

    --widthImgPreorder: 124px;

    @include square(var(--widthImgPreorder));

    overflow: hidden;
    border-radius: 8px;
    background: var(--color-light-gray);

    @include tablet {
      --widthImgPreorder: 116px;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
    }
  }

  // .preorder__link

  &__link {
    grid-column: 2 / 5;
    padding-top: 14px;
    color: var(--color-dark-navy);
    letter-spacing: 0.224px;
    transition: color 0.3s ease 0s;
    max-width: 90%;
    cursor: pointer;
    @include hover {
      color: var(--color-light-blue);
    }
  }

  // .preorder__stepper

  &__stepper {
    grid-column: 2 / 3;
    grid-row: 2 / 3;
    @include tablet {
      grid-column: 1 / 2;
    }
    @include mobile {
      grid-column: 2 / 3;
    }
    @include mobile-m {
      grid-column: 1 / 2;
    }
  }

  // .preorder__price

  &__price {
    grid-column: 3 / 5;
    grid-row: 2 / 3;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    @include mobile {
      grid-column: 4 / 6;
    }
    @include mobile-m {
      grid-column: 2 / 6;
    }
    span {
      color: var(--color-dark-navy);
      font-size: 16px;
      line-height: 1.4;
      letter-spacing: 0.256px;
      span {
        font-size: 12px;
        line-height: 120%;
        letter-spacing: 0.192px;
      }
    }
    p {
      color: var(--color-dark-gray);
      font-size: 12px;
      letter-spacing: 0.192px;
    }
  }
}

.stepper {
  position: relative;
	display: flex;
  justify-content: space-between;
	align-items: center;
	width: 127px;
	height: 36px;
	user-select: none;

  // .stepper__field

  &__field {
    overflow: hidden;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    border: 1px solid var(--color-medium-gray);
    border-radius: 4px;
    max-width: 64px;
    width: 100%;
    height: 100%;
    background-color: var(--color-white);
  }

  // .stepper__input

  &__input {
    border: none;
    height: 100%;
    outline: none;
    width: 100%;
    text-align: center;
    color: var(--color-dark-navy);
    font-size: 14px;
    letter-spacing: 0.224px;
  }

  // .stepper__btn--disabled

  &__btn--disabled {
    opacity: 0.2;
	  pointer-events: none;
  }
}

// Page checkout end ========================================================================================================================================================

// Footer start ========================================================================================================================================================

.footer {
  padding: 4px 8px;
  background-color: var(--color-light-gray);
  .container {
    padding: 0;
  }

  // .footer__inner

  &__inner {
    padding-inline: 24px;
    border-radius: 8px;
    background-color: var(--color-dark-navy);
  }

  // .footer__body

  &__body {
    padding-block: fluid(72, 48) 32px;
    display: grid;
    grid-template-columns: repeat(20, 1fr);
    column-gap: 16px;
    @include tablet {
      grid-template-columns: repeat(2, 1fr);
      row-gap: 80px;
    }
    @include mobile-m {
      grid-template-columns: 1fr;
      row-gap: 32px;
    }
  }

  // .footer__column

  &__column {
    &:first-child {
      grid-column: span 9;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      @include laptop {
        grid-column: span 7;
      }
      @include tablet {
        grid-column: span 1;
        order: 1;
      }
      @include mobile-m {
        grid-column: auto;
      }
    }
    &:nth-child(2) {
      grid-column: span 7;
      @include tablet {
        grid-column: span 2;
        order: 3;
      }
      @include mobile-m {
        grid-column: auto;
      }
    }
    &:last-child {
      grid-column: span 4;
      justify-items: end;
      text-align: right;
      @include laptop {
        grid-column: span 6;
      }
      @include tablet {
        grid-column: span 1;
        order: 2;
      }
      @include mobile-m {
        grid-column: auto;
        justify-items: start;
        text-align: left;
      }
    }
  }

  // .footer__logo

  &__logo {
    @include mobile-m {
      margin-bottom: 24px;
      img {
        width: 161px;
      }
    }
  }

  // .footer__text

  &__text {
    p {
      @include fluid-text(16, 12);
      color: var(--color-white);
      font-weight: 400;
      letter-spacing: fluid(0.256, 0.192);
      max-width: 458px;;
      @include mobile-m {
        font-weight: 300;
      }
    }
  }

  // .footer__column-menus

  &__column-menus {
    display: flex;
    column-gap: 100px;
    @include laptop {
      column-gap: 50px;
    }
    @include tablet {
      column-gap: fluid(100, 50);
    }
    @include mobile-s {
      flex-direction: column;
      row-gap: 56px;
    }
    ul {
      margin: 0;
      padding: 0;
      list-style: none;
      li {
        &:not(:last-child) {
          margin-bottom: fluid(24, 20);
        }
        a {
          color: var(--color-white);
          font-size: 14px;
          letter-spacing: 0.224px;
          text-decoration: none;
          @include hover {
            text-decoration: underline;
          }
        }
      }
    }
  }

  // .footer__social

  &__social {
    margin-bottom: 24px;
    .social__item {
      width: 48px;
      height: 48px;
      svg {
        width: 20px;
        height: 20px;
      }
    }
  }

  // .footer__email

  &__email {
    padding: 16px 16px 16px 74px;
    color: var(--color-white);
    font-size: 14px;
    letter-spacing: 0.224px;
    border: 2px solid var(--color-light-gray);
    border-radius: 999px;
    background: url('../img/envelope.svg') 20px center/24px 24px no-repeat;
    transition: box-shadow 0.3s ease 0s;
    @include hover {
      box-shadow: 0 0 10px rgba(255,255,255,0.5);
    }
    @include mobile-s {
      padding-left: 56px;
    }
  }

  // .footer__copyright

  &__copyright {
    padding-block: 32px;
    border-top: 1px solid var(--color-dark-gray);
    p {
      color: var(--color-medium-gray);
      text-align: center;
      font-size: 14px;
      @include mobile-s {
        text-align: left;
      }
    }
  }
}

.footer__checkout {
  .footer__copyright {
    border-top: none;
    p {
      text-align: left;
    }
  }
}

// Footer end ========================================================================================================================================================




