:root {
  // base
  --font-family: "eUkraine", sans-serif;
  --content-width: 1540px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors

  --color-deep-blue: #0F5EBB;
  --color-golden-yellow: #FFD719;
  --color-dark-navy: #1C2F43;
  --color-light-blue: #5A9BEF;
  --color-warm-yellow: #FFE46E;
  --color-white: #ffffff;
  --color-light-gray: #F6F7F9;
  --color-medium-gray: #D0D0D0;
  --color-dark-gray: #6B6B6B;
  --color-error: #D32F2F;
  --color-success: #388E3C;
}
