@use 'sass:math';
@use 'functions' as *;
@use './mixins/fluid' as *;
@use './mixins/disable-mob-hover' as *;
@use './mixins/_breakpoint' as *;

html {
  &.is-lock {
    overflow: hidden;
  }
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

.page__body {
  margin: 0;
  min-width: 375px;
  min-height: 100%;
  font-size: 16px;
  font-weight: 300;
  font-style: normal;
  line-height: 1.2;
}

img {
  object-fit: cover;
}

.site-container {
  //overflow: hidden; // If you use position: sticky on your site, remove this setting.
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

a,
button,
label,
input,
textarea,
select,
svg * {
  transition-duration: var(--transition-duration);
}

a {
  color: inherit;

  &[class] {
    text-decoration: none;
  }
}

.visually-hidden {
  position: absolute !important;
  width: 1px !important;
  height: 1px !important;
  margin: -1px !important;
  border: 0 !important;
  padding: 0 !important;
  white-space: nowrap !important;
  clip-path: inset(100%) !important;
  clip: rect(0 0 0 0) !important;
  overflow: hidden !important;
}

// Buttons

.btn {
  color: var(--color-white);
  font-size: 14px;
  font-weight: 700;
  border-radius: 999px;
  padding: 12px 24px;
  transition: background-color 0.3s ease 0s, border 0.3s ease 0s;
  &-blue {
    background: var(--color-deep-blue);
    border: 2px solid var(--color-deep-blue);
    @include hover {
      background: var(--color-light-blue);
      border: 2px solid var(--color-light-blue);
    }
  }
  &-navy {
    background: var(--color-dark-navy);
    @include hover {
      background: linear-gradient(0deg, rgba(0, 0, 0, 0.16) 0%, rgba(0, 0, 0, 0.16) 100%), #1C2F43;

    }
  }
  &-arrow {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &:disabled {
    background: var(--color-medium-gray);
    cursor: default;
  }
}

.btn-outline {
  font-size: 14px;
  font-weight: 700;
  padding: 12px 24px;
  border-radius: 999px;
  transition: background-color 0.3s ease 0s, color 0.3s ease 0s;
  &-navy {
    color: var(--color-dark-navy);
    border: 2px solid var(--color-dark-navy);
    @include hover {
      background-color: var(--color-dark-navy);
      color: var(--color-white);
    }
  }
  &-blue {
    color: var(--color-deep-blue);
    border: 2px solid var(--color-deep-blue);
    @include hover {
      background-color: var(--color-deep-blue);
      color: var(--color-white);
    }
  }
  &-arrow {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &:disabled {
    border: 2px solid var(--color-medium-gray);
    color: var(--color-medium-gray);
    cursor: default;
    background-color: var(--color-white);
  }
}

.title {
  @include fluid-text(56, 28);

  color: var(--color-dark-navy);
  font-family: "eUkraineHead";
  letter-spacing: fluid(0.7, 0.392);
  &-white {
    color: var(--color-white);
  }
  @include tablet {
    font-family: var(--font-family, sans-serif);
  }
}

.overlay {
  position: fixed;
  inset: 0;
  background-color: rgba(#1C2F43, 0.4);
  opacity: 0;
  visibility: hidden;
  &.open {
    opacity: 1;
    visibility: visible;
  }
}
